.sidebar {
    background: #ECECF9;
    padding: 24px;

    p {
        font-family: 'Kaushan Script', sans-serif;
        font-size: 22px;
        font-weight: 700;
    }

    .containerNumberOfCards{
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100%;
    }

    @media (max-width: 1150px) {
        padding: 0 10px 10px 10px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
    }

    @media (max-width: 400px) {
        flex-direction: column;
    }

}