.table{position:relative;box-shadow:0 4px 14px rgba(45,46,70,.15);width:100%;background:#fff;text-align:left;color:#000;font-size:14px;line-height:16px;margin:24px 0;overflow:auto;flex-grow:1;scrollbar-width:thin}@media(max-width: 460px){.table{margin:10px 0}}.table::-webkit-scrollbar{width:4px;background-color:#e6d4de}.table::-webkit-scrollbar-thumb{background-color:#9890c7}.item{display:grid;grid-template-columns:1fr 1fr 120px 120px;align-items:center;grid-gap:0 30px;padding:16px 24px}@media(max-width: 900px){.item{grid-template-columns:1fr 1fr 120px 60px;grid-gap:10px}}@media(max-width: 630px){.item{grid-template-columns:1fr 150px 70px;grid-gap:20px}}@media(max-width: 560px){.item{grid-template-columns:1fr 120px 100px;grid-gap:16px}}@media(max-width: 460px){.item{grid-template-columns:1fr 180px;grid-gap:16px}}.label{cursor:pointer}.label:hover{color:#5d079f}.itemMy{display:grid;grid-template-columns:1fr 1fr 120px 120px 120px;align-items:center;grid-gap:0 30px;padding:16px 24px}@media(max-width: 900px){.itemMy{grid-template-columns:1fr 1fr 120px 60px;grid-gap:10px}}@media(max-width: 630px){.itemMy{grid-template-columns:1fr 180px 100px 30px}}@media(max-width: 560px){.itemMy{grid-template-columns:1fr 230px}}@media(max-width: 460px){.itemMy{grid-template-columns:1fr 180px}}.item:nth-child(odd),.itemMy:nth-child(odd){background:#f8f7fd}.item.header,.itemMy.header{background:#ececf9;font-weight:700;position:sticky;top:0}.item a,.itemMy a{color:inherit;font-weight:700;text-decoration:none}.button,a.button{font-weight:600;color:#5d079f;padding:6px;border:none;background:#d7d8ef;border-radius:2px;cursor:pointer;display:inline-block;margin-right:6px;transition:.4s all}.button:hover,a.button:hover{background:#5d079f;color:#fff}.delete{background:#f1453d;color:#fff}.delete:hover{background:darkred}.header div{cursor:pointer}.header div.active:after{content:"";border:5px solid rgba(0,0,0,0);border-top-color:#2d2e46;display:inline-block;vertical-align:bottom;margin-left:4px}.header div.active.up:after{transform:rotate(180deg) translateY(5px)}.item a,.itemMy a{color:inherit;font-weight:700;text-decoration:none;word-break:break-all}.rating_result{display:inline-block;vert-align:bottom;font-size:0}@media(max-width: 630px){.rating_result{display:none}}.rating_result span{padding:0;font-size:12px;line-height:1;color:#dcb87e}.rating_result>span:before{content:"â˜…"}.rating_result>span.active{color:#5d079f}.textArea{margin-bottom:20px;text-align:left}@media(max-width: 900px){.itemMy.header div:last-child{display:none}}@media(max-width: 630px){.item.header div:last-child{display:none}.itemMy.header div:nth-last-child(2){display:none}.itemMy .rating_result{display:none}}@media(max-width: 560px){.itemMy.header div:nth-last-child(3){display:none}}@media(max-width: 460px){.item div:last-child{grid-column:1/3}.itemMy div:last-child{grid-column:1/3}}