.error {
  min-height: 20px;
  font-size: 15px;
  color: red;
  margin-bottom: 5px;
}

.avatar {
  text-align: center;
  padding-bottom: 10px;

  img {
    border-radius: 50%;
    height: 200px;
    width: 200px;
    object-fit: cover;
    border: 4px outset #e7a3a3;
    background-color: #afa5a5;

    &:hover {
      border: 4px inset #e7a3a3;
    }

  }
}


.profileTitle {
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  color: #2D2E46;
  font-family: 'Kaushan Script', sans-serif;
  font-size: 28px;
}

.info {
  display: flex;
  padding-bottom: 10px;
  padding-left: 10px;
  width: 100%;
  word-wrap: break-word;
  color: #555555;
}

.btnWrapper {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  gap: 10px;
}