.inputContainer {
  position: relative;
  display: block;

  .superInput {
    background: #FFFFFF;
    border: 1px solid #BCBEC0;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 6px 15px;
    width: 100%;
    height: 50px;

    &:focus {
      outline: none;
      border: 1px solid #21268F;
    }

  }

  .errorInput {
    outline: none;
    border-right: 5px solid red;
  }

  .error {
    display: flex;
  }

  .eye {
    position: absolute;
    height: 24px;
    width: 24px;
    right: 16px;
    top: 50%;
    border: none;
    transform: translateY(-50%);
    background: url(../../../assets/images/no-view.svg) no-repeat center/contain;
    cursor: pointer;
    z-index: 2;
  }

  .eyeShow {
    background: url(../../../assets/images/view.svg) no-repeat center/contain;
  }

}


